import wx from 'weixin-js-sdk'
import axios from 'axios'
import {savaToLocal, loadFromlLocal} from  '../common/js/store'
import {model} from './model'
let utils_wx = {
  data: {
    id: 1,
    type: 2,
    desc: '',
    title: '',
    link: '',
    imgUrl: '',
    success: function (res) {
      console.log(res)
      console.log('')
      // this.link = 'http://wxtv.candytime.com.cn/weChat/a?id=37&type=2'
      console.log({
        id: this.id,
        type: this.type
      })
      model.weChatShare({
        id: this.id,
        type: this.type
      }).then((res) => {
        console.log('')
        console.log(res)
      })
    },
    cancel: function (res) {
      console.log(res)
      console.log('')
    }
  },
  signData: {

  },
  loadWx: function () {
    console.log('loadWxloadWxloadWxloadWxloadWxloadWxloadWx')
    console.log(this.signData)
    let that = this
    this.signData = JSON.parse(that.signData)
    console.log(this.signData)
    console.log(this.signData.appId)
    console.log('')
    wx.config({
      debug: false, // ,apialertpclogpc
      appId: this.signData.appId,
      timestamp: this.signData.timestamp,
      nonceStr: this.signData.nonceStr,
      signature: this.signData.signature,
      jsApiList: [//  JS,api
        'checkJsApi',
        'onMenuShareTimeline',
        'onMenuShareAppMessage',
        'onMenuShareQQ',
        'onMenuShareWeibo'
      ]
    })
    this.edit_data()
  },
  //  desc   imgUrl  title  link
  edit_data(datat) {
    console.log('edit_dataedit_dataedit_dataedit_data')
    console.log(datat)
    if(datat){
      this.data.desc = datat.desc
      this.data.imgUrl = datat.imgUrl
      this.data.title =  datat.title
      this.data.link = datat.link
    }

    if (!this.data.title) {
      this.data.title = ''
    }
    if (!this.data.desc) {
      this.data.desc = ''
    }
    if (!this.data.link) {
      this.data.link = 'http://wxtv.candytime.com.cn/weChat/a?id=37&type=2'
    }
    if (!this.data.imgUrl) {
      this.data.imgUrl = 'http://60.205.216.188/logo.jpg'
    }
    console.log('')
    console.log(this.data)
    console.log(JSON.stringify(this.data))
    let that = this
    wx.onMenuShareAppMessage(that.data)//  
    wx.onMenuShareTimeline(that.data)//  
    wx.onMenuShareQQ(that.data)//  QQ
    wx.ready(function () {
      console.log('ready')
      console.log(that.data)
      wx.onMenuShareAppMessage(that.data)//  
      wx.onMenuShareTimeline(that.data)//  
      wx.onMenuShareQQ(that.data)//  QQ
    })

  },
  // 
  fenxianglianjie: function() {
    console.log('weixin.js')
    console.log('ur')
    console.log(location.href.split('#')[0])
    console.log('')
    axios({
      url: 'http://wxtv.candytime.com.cn/api/wechat/get_config?url=' + location.href.split('#')[0],
      method: 'get',
      headers: {
        'token': loadFromlLocal('token')
      },
      data: {
        url: location.href.split('#')[0]
      }
    }).then((res) => {
      console.log('')
      console.log(res.data.data)
      this.signData = res.data.data
      this.loadWx()
    })
  }
}

export {utils_wx}



